<template>
  <div v-if="LoginData.aikodaiID == null">
    <LoginPage :aikodai-id="LoginData.aikodaiID" :password="LoginData.password" @login-ok="LoadLoginData"></LoginPage>
  </div>
  <div v-else>
    <v-app id="inspire">
      <v-app-bar color="white" flat  :class="getPadding()" >
        <v-img contain src="../img/logo.png" :width="getIconSize()" :max-width="getIconSize()"
          :height="getIconSize()"></v-img>
        <v-spacer></v-spacer>
        <v-tabs centered color="grey-darken-2" v-model="tab" align-tabs="center" center-active stacked>
          <v-tab v-for="link in links" :key="link.id" :value="link.id" color="pink-lighten-1" :class="getPadding2()"
            :max-width="getTabsWidth()">
            <v-icon>{{ link.icon }}</v-icon>
            <div v-if="!isDisplayxs()">{{ link.title }}</div>
            <div v-else class="text-caption">{{ link.title }}</div>
          </v-tab>
        </v-tabs>
        <v-spacer></v-spacer>
        <v-btn rounded variant="outlined" :size="getButtonSize()" :class="getPaddingBtn()" @click="showId()">
          
          <v-icon>mdi-account</v-icon>
          <div v-if="!isDisplayxs()">{{ LoginData.aikodaiID }}</div>
          <div v-else-if="showFlg" class="text-caption">{{ LoginData.aikodaiID }}</div>
        </v-btn>
      </v-app-bar>

      <v-main class="bg-grey-lighten-3">
        <v-window v-model="tab">
          <v-window-item v-for="link in  links " :key="link.id" :value="link.id">
            <!-- Mapsのウィンドウ -->
            <v-container v-if="link.id == 1">
              <SchoolMaps></SchoolMaps>
            </v-container>
            <!-- TimeTableのウィンドウ -->
            <v-container v-if="link.id == 2">
              <TimeTable :aikodai-id="LoginData.aikodaiID" :password="LoginData.password" :time-table="LoginData.timeTable" @to-login-page="InitLogin"></TimeTable>
            </v-container>
            <!-- WelcomePageのウィンドウ -->
            <v-container v-if="link.id == 3">
              <WelcomePage :now-ait="Now_ait" :now-yakusa="Now_yakusa" :aikodai-id="LoginData.aikodaiID" :time-table="LoginData.timeTable"></WelcomePage>
            </v-container>
            <!-- Bus Scheduleのウィンドウ-- > -->
            <v-container v-if="link.id == 4">
              <BusSchedule></BusSchedule>
            </v-container>
            <!-- Kitchen Carのウィンドウ -->
            <v-container v-if="link.id == 5">
              <KitchenCar></KitchenCar>
            </v-container>
            <!-- Informationのウィンドウ -->
            <v-container v-if="link.id == 6">
              <InforMation></InforMation>
            </v-container>
          </v-window-item>
        </v-window>
      </v-main>
      <v-footer class="bg-grey-lighten-1" dark app>
        <v-row justify="center" no-gutters>
          <div>
            {{ this.Now_yakusa }}
            {{ this.Now_ait  }}
          </div>
          <!-- <div v-if="!isDisplayxs()">
            <v-btn color="white" variant="text" class="mx-2" rounded="xl" v-for=" link  in   links  " :key="link.id">
              <div :class="getTextSize()">{{ link.title }}</div>
              
            </v-btn>
          </div> -->
          <v-col class=" text-center mt-4" cols="12" :class="getTextSize()">
            {{ new Date().getFullYear() }} — <strong>AITPlus</strong>
          </v-col>
        </v-row>
      </v-footer>
    </v-app>
  </div>
</template>


<script>
import SchoolMaps from './SchoolMaps.vue'
import TimeTable from './TimeTable.vue'
import WelcomePage from './WelcomePage.vue'
import BusSchedule from './BusSchedule.vue'
import KitchenCar from './KitchenCar.vue'
import InforMation from './InforMation.vue'
import LoginPage from './LoginPage.vue'
import { getCurrentInstance } from 'vue';

const usevuetify = () => {
  const instance = getCurrentInstance()
  if (!instance) {
    throw new Error(`vuetify instance doesn't exist`)
  }
  return instance.proxy.$vuetify
}
export default {
  name: 'App',
  data: () => ({
    tab: null,
    links: [
      { id: 1, title: 'Maps', icon: 'mdi-map-marker' },
      { id: 2, title: 'TimeTable', icon: 'mdi-timetable' },
      { id: 3, title: 'Home', icon: 'mdi-home' },
      { id: 4, title: 'Bus Schedule', icon: 'mdi-bus-clock' },
      { id: 5, title: 'Kitchen Car', icon: 'mdi-food' },
      { id: 6, title: 'Information', icon: 'mdi-information' },
    ],
    LoginData: {
      aikodaiID: null,
      password: null,
      timeTable: null,
    },
    showFlg: false,
  }),
  components: {
    /* eslint-disable */
    SchoolMaps,
    TimeTable,
    WelcomePage,
    KitchenCar,
    InforMation,
    BusSchedule,
    LoginPage,
    /* eslint-disable */
  },
  // ここにメソッドを書く
  methods: {
    LoadLoginData(id, ps,tt) {
      this.LoginData.aikodaiID = id
      this.LoginData.password = ps
      this.LoginData.timeTable = tt
    },
    InitLogin() {
      this.LoginData.aikodaiID = null
      this.LoginData.password = null
    },
    
    isDisplayxs: function () {
      const vuetify = usevuetify();
      return vuetify.display.xs;
    },
    getButtonSize: function () {
      if (this.isDisplayxs()) {
        return 'small'
      } else {
        return 'large'
      }
    },
    getPadding: function () {
      if (this.isDisplayxs()) {
        return 'px-0.5'
      } else {
        return 'px-3'
      }
    },
    getPadding2: function () {
      if (this.isDisplayxs()) {
        return 'pa-0.5'
      } else {
        return 'pa-6'
      }
    },
    getPaddingBtn: function () {
      if (this.isDisplayxs()) {
        return 'pa-0 ma-0'
      } else {
        return ''
      }
    },
    getIconSize: function () {
      if (this.isDisplayxs()) {
        return '25'
      } else {
        return '50'
      }
    },
    getTabsWidth: function () {
      if (this.isDisplayxs()) {
        return '25'
      } else {
        return 'auto'
      }
    },
    getTextSize: function () {
      if (this.isDisplayxs()) {
        return 'text-caption'
      } else {
        return 'text-body-2'
      }
    },
    showId: function () {
      this.showFlg = !this.showFlg
    },
  },
  computed: {
    
  },
  created: function () {
    for(let x in this.links){
      this.tab = this.links[x].id
    }
    this.tab = 3
    // デバッグモード
    // this.LoginData.aikodaiID = 'test'
    // this.LoginData.password = 'test'
  },
}
</script>

<style>
.sheet {
  padding: 100px;
}
</style>