                                            現在時刻
                                        </span>
                                    </v-card-title>
                                    <v-card-text>

                                        <div class="pa-4 text-center">
                                            <span class=" font-weight-regular" :class="getTextSizeClock()">
                                                {{ this.clock }}
                                            </span>

                                        </div>
                                    </v-card-text>
                                </v-card>
                            </v-col>
                            <v-col cols="6">
                                <v-card class="mx-auto" max-width="500" variant="elevated" color="">
                                    <v-card-title class=" justify-space-between">
                                        <v-icon class="pb-2" :size="isDisplayxs() ? 'x-small' : 'auto'">mdi-bus</v-icon>
                                        <span class="  text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                            次のバスの予定時刻
                                        </span>
                                    </v-card-title>
                                    <v-card-text>
                                        <v-row justify="center">

                                            <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                <div class="text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                    八草発
                                                </div>

                                                <div class="text-center"
                                                    :class="isDisplayxs() ? 'text-body-2 pt-0.5' : 'pt-3'">
                                                    {{ this.Now_yakusa }}
                                                </div>
                                            </v-col>

                                            <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                <div class="text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                    愛工大発
                                                </div>

                                                <div class="text-center"
                                                    :class="isDisplayxs() ? 'text-body-2 pt-0.5' : 'pt-3'">
                                                    {{ this.Now_ait }}
                                                </div>
                                            </v-col>

                                        </v-row>


                                    </v-card-text>
                                </v-card>
                            </v-col>
                            <v-col cols="6">
                                <v-card class="mx-auto" max-width="500" variant="elevated" color="">
                                    <v-card-title class=" justify-space-between">
                                        <v-icon class="pb-2" :size="isDisplayxs() ? 'x-small' : 'auto'">mdi-school</v-icon>
                                        <span class="  text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                            次の講義
                                        </span>
                                    </v-card-title>
                                    <v-card-text>
                                        <div v-if="this.AikodaiId != 'guest' && this.AikodaiId != ''">

                                            <v-row justify="center" v-if="Now_lecture.period != 0">

                                                <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                    <div class="text-center"
                                                        :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                        講義名
                                                    </div>

                                                    <div class="text-center"
                                                        :class="isDisplayxs() ? 'text-caption pt-0.5' : 'pt-3'">
                                                        {{ this.Now_lecture.name }}
