                                                <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                    <div class="text-center"
                                                        :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                        場所
                                                    </div>

                                                    <div class="text-center"
                                                        :class="isDisplayxs() ? 'text-body-2 pt-0.5' : 'pt-3'">
                                                        {{ this.Now_lecture.locate }}
                                                    </div>
                                                </v-col>

                                            </v-row>
                                            <v-row justify="center" v-else>
                                                <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                    <div class="text-center"
