                                                        今日の講義は終了しました
                                                        <br>
                                                        お疲れ様でした!
                                                    </div>
                                                </v-col>
                                            </v-row>
                                        </div>
                                        <div v-else class="text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                            この機能を利用するにはログインが必要です
                                        </div>
                                    </v-card-text>
                                </v-card>
                            </v-col>
                            <v-col cols="12">
                                <v-card class="mx-auto" max-width="500" variant="elevated" color="">
                                    <v-card-title class=" justify-space-between">
                                        <v-icon class="pb-2" :size="isDisplayxs() ? 'x-small' : 'auto'">mdi-food</v-icon>
                                        <span class="  text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                            今日の移動販売車
                                        </span>
                                    </v-card-title>
                                    <v-card-text>
                                        <v-row justify="center">

                                            <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                <div class="text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                    一号館前
                                                </div>

                                                <div class="text-center"
                                                    :class="isDisplayxs() ? 'text-body-2 pt-0.5' : 'pt-3'">
                                                    {{ this.CarFood[0] }}
                                                </div>
                                            </v-col>

                                            <v-col xs="12" :class="isDisplayxs() ? 'pa-0 pt-2' : 'auto'">
                                                <div class="text-center" :class="isDisplayxs() ? 'text-caption' : 'auto'">
                                                    セントラルガーデン
                                                </div>

                                                <div class="text-center"
                                                    :class="isDisplayxs() ? 'text-body-2 pt-0.5' : 'pt-3'">
                                                    {{ this.CarFood[1] }}
                                                </div>
                                            </v-col>

